@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@500&display=swap);
* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  font-family: Dosis;
  box-sizing: border-box;
  scrollbar-width: none;
  -ms-overflow-style: none;
  scroll-behavior: smooth;
}
::-webkit-scrollbar {
  display: none;
}

body {
  background-image: url(/SampleSpace/static/media/witch_magic.7e8b97b8.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

.loader {
  margin: 20px;
}

.nav {
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  position: fixed;
  z-index: 10;
  overflow: hidden;
  top: 0;
}

.header-height {
  height: 55px;
}

.center {
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.head-wrapper {
  width: 80%;
  margin: 0 auto;
}

.title-wrapper {
  background-color: rgba(44, 44, 44, 0.5);
  color: white;
  width: 10vw;
  font-family: Karla;
  letter-spacing: 2px;
}

.nav-link {
  color: #f4f4f9;
  padding: 0 20px;
  vertical-align: middle;
  float: right;
  transition: color 0.1s;
}

.nav-link:hover {
  color: #3895d3;
}

.panel {
  background-color: rgba(0, 0, 0, 0.7);
  width: 80%;
  margin: 0 auto;
  padding-top: 100px;
  box-shadow: 2px 2px 10px 1px rgb(0, 0, 0);
}

.jump {
  position: fixed;
  bottom: 8vh;
  right: 16vw;
  width: 40px;
  height: 30px;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 3px;
  transition: all 0.2s;
}

.jump img {
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.jump:hover {
  background-color: rgba(255, 255, 255, 1);
}

.top-header {
  color: white;
  font-size: 35px;
  text-align: center;
}
.top-poster {
  padding: 30px !important;
}
.rank-box {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  z-index: 3;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

.rank {
  color: #fff;
  opacity: 0;
  font-size: 70px;
  z-index: 4;
  transition: opacity 0.2s;
}

.rank-box:hover div {
  opacity: 0.8;
}

.banner {
  text-align: center;
  width: 600px;
  height: 10%;
  margin: 0 auto 8vh auto;
}

.banner img {
  height: 100%;
  width: 100%;
  border: 1px solid white;
}

.anime-wrapper {
  margin: 0 auto;
  width: 60%;
  margin-top: 30px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.poster {
  display: block;
  width: 33%;
  height: 300px;
  padding: 17px;
}

.content-wrapper {
  width: 100%;
  height: 100%;
  position: relative;
  border: 2px solid white;
}

.content-wrapper img {
  width: 100%;
  height: 100%;
  z-index: 0;
  transition: -webkit-filter 0.2s;
  transition: filter 0.2s;
  transition: filter 0.2s, -webkit-filter 0.2s;
}

.content-wrapper:hover img {
  -webkit-filter: blur(0) brightness(0.5);
          filter: blur(0) brightness(0.5);
}

.anime-info {
  display: flex;
  align-content: center;
  justify-content: center;
  color: white;
  font-size: 0.7rem;
  font-weight: 800;
  background-color: rgba(0, 0, 0, 0.7);
  text-align: center;
  position: absolute;
  padding: 4px;
  top: 80%;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 2;
}

.cross-box {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 30px;
  height: 30px;
  left: 86%;
  z-index: 3;
  border-bottom-left-radius: 2px;
  /* background: red; */
  cursor: pointer;
}
.spoiler {
  width: 50%;
  height: 50%;
  z-index: 4;
  /* background-color: black; */
}
.cross {
  color: #fff;
  font-family: Quicksand;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.2s;
}

.spoiler:hover div {
  opacity: 1;
}

.input {
  text-align: center;
  margin-bottom: 20px;
  margin-top: 50px;
}
.input input {
  box-sizing: border-box;
  width: 400px;
  height: 50px;
  padding: 10px;
}

.input p {
  margin-top: 3vh;
  color: white;
  font-size: 1.5em;
}

.profile {
  color: white;
  margin: 60px auto 70px auto;
  width: 60%;
  display: flex;
  -webkit-transform: translateX(50px);
          transform: translateX(50px);
}

.profile-url {
  width: 200px;
  height: 200px;
  margin-right: 50px;
}

.profile-url:hover img {
  border: 2px solid #3895d3;
}
.profile-pic {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border: 2px solid white;
  transition: border 0.2s;
}

.info {
  padding: 30px;
}

.info li {
  list-style-type: disc;
  font-size: 20px;
  margin-bottom: 15px;
}

