.profile {
  color: white;
  margin: 60px auto 70px auto;
  width: 60%;
  display: flex;
  transform: translateX(50px);
}

.profile-url {
  width: 200px;
  height: 200px;
  margin-right: 50px;
}

.profile-url:hover img {
  border: 2px solid #3895d3;
}
.profile-pic {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border: 2px solid white;
  transition: border 0.2s;
}

.info {
  padding: 30px;
}

.info li {
  list-style-type: disc;
  font-size: 20px;
  margin-bottom: 15px;
}
